@import "./vendor/normalize.css";

* {
  font-family: Roboto;
  color: #111;
  -webkit-font-smoothing: antialiased;
}

a {
  color: #005BFF;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

html, body {
  height: 100%;
  background-color: #182026;
}

.background {
  background-image: url(../images/background.jpg);
  background-size: cover;
  min-height: 600px;
  height: 100vh;
  width: 100%;
  position: relative;
}

.background .gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -moz-linear-gradient(top, rgba(24,32,38,0) 0%, rgba(24,32,38,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(24,32,38,0) 0%,rgba(24,32,38,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(24,32,38,0) 0%,rgba(24,32,38,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00182026', endColorstr='#182026',GradientType=0 ); /* IE6-9 */
}

.container {
  box-sizing: border-box;
  max-width: 1140px;
  margin: 0 auto;
  position: relative;
  height: 100%;
}


.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0;
}

.logo > a {
  font-family: Roboto Mono;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
}

.buttons {
  margin: 0 -10px;
}

.buttons a {
  font-size: 18px;
  color: #fff;
  padding: 9px 30px 10px;
  font-family: Roboto;
  font-weight: 100;
  text-decoration: none;
  margin: 0 10px;
  border-radius: 4px;
  transition: 0.1s linear all;
}

.buttons a:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.buttons a.signIn {
  background-color: #137CBD;
}

.buttons a.signIn:hover {
  background-color: #2B95D6;
}


.landing {
  padding: 30px 0 145px;
  text-align: center;
}

.landing .logo {
  height: 80px;
  width: 80px;
  margin: 0 auto 60px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
}

.landing .name {
  font-family: Raleway;
  font-size: 72px;
  font-weight: 100;
  color: #fff;
}

.landing .description {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 300;
  color: #fff;
  margin-top: 20px;
}